import React, {
  useCallback,
  useEffect,
  useState,
  useRef
} from 'react'
import styles from './dynamicTurntable.module.sass'

import bg from 'assets/images/bg.jpg'
        
const DynamicTurntable = () => {
  const [canvas, setCanvas] = useState(null)
  const [ctx, setCtx] = useState(null)
  const time = useRef(0)

  const [imgList, setImgList] = useState(null)

  const blockWidth = 200

  const PI = Math.PI
  const PI2 = Math.PI * 2

  const globalControl = {
    scale: 1,
    width: 4000,
    height: 4000,
    foodMax: 500,
    playerMax: 50,
    FPS: 30,

    players: [],

    angle: 0,
    angleSpeed: 1,
  }

  let ww = window.innerWidth - 3
  let wh = window.innerHeight - 3

  window.addEventListener('resize', () => {
    if (!canvas) return 
    canvas.width = ww
    canvas.height = wh
  })

  const color = {
    black: '#000',
    bgWhite: '#eee',
    blue: '#036faf',
    gold: '#d3b889',
  }

  const drawCircle = useCallback((posX, posY, r, fillColor, strokeColor) => {
    ctx.beginPath()
    ctx.arc(posX, posY, r, 0, 2 * Math.PI)
    ctx.closePath()
    if (fillColor) {
      ctx.fillStyle = fillColor
      ctx.fill()
      return 
    }
    ctx.strokeStyle = strokeColor
    ctx.stroke()
  }, [ctx])

  // const drawCD = useCallback((posX, posY, bgColor, draw, time) => {
  //   ctx.save()
  //   ctx.translate(ww / 2, wh / 2)
  //   // ctx.fillStyle = color.black
  //   // ctx.fillRect(0, 0, 600, 600)

  //   ctx.fillStyle = color.black
  //   ctx.beginPath()
  //   ctx.arc(0, 0, (ww / 3), 0, 2 * Math.PI)
  //   ctx.closePath()
  //   ctx.fill()

  //   // ctx.translate(100, 100) // 將座標移動到矩形正中間
  //   // draw()
  //   ctx.restore()
  // }, [ctx])

  const paint = useCallback(() => {
    ctx.clearRect(0, 0, ww, wh)
    time.current += 1
    // let fastTime = time.current
    // let sTime = parseInt(time.current / 10)

    ctx.save()
    ctx.translate(ww / 2, wh / 2)
    // 唱片底色與陰影
    ctx.shadowBlur = 100
    ctx.shadowColor = 'rgba(0, 0, 0, 0.3)'
    drawCircle(0, 0, (ww / 3), color.black)
    ctx.shadowBlur = 0
    drawCircle(0, 0, (ww / 7), color.blue)

    // 選轉效果
    ctx.rotate(globalControl.angle)
    globalControl.angle += globalControl.angleSpeed
    globalControl.angle *= 0.98

    // 中間底圖
    ctx.globalCompositeOperation = 'color-burn'
    ctx.drawImage(imgList, -(ww / 3) / 2, -(ww / 3) / 2)
    ctx.globalCompositeOperation = 'source-over'

    // 唱片裝飾
    ctx.lineWidth = 10
    drawCircle(0, 0, (ww / 8), null, color.gold)
    drawCircle(0, 0, (ww / 25), color.black)
    drawCircle(0, 0, (ww / 50), color.bgWhite)

    ctx.lineWidth = 1
    for(let i = 0; i < 40; i++) {
      drawCircle(0, 0, (ww / 3) * i / 40, null, `rgba(255, 255, 255, ${i % 5 / 20})`)
    }



    // ctx.translate(100, 100) // 將座標移動到矩形正中間
    // draw()
    ctx.restore()

  }, [ctx, time.current])

  useEffect(() => {
    if (!ctx) return
    canvas.width = ww
    canvas.height = wh
    setInterval(paint, 30)
  }, [ctx, ww, wh])

  const initCanvas = useCallback(() => {
    const myCanvas = document.getElementById('myCanvas')
    setCanvas(myCanvas)
    if (!canvas) return
    const ctx = canvas.getContext('2d')
    setCtx(ctx)

    const imageObj1 = new Image()
    imageObj1.src = bg
    setImgList(imageObj1)
  }, [canvas])

  useEffect(() => {
    initCanvas()
  }, [canvas])

  return (
    <div className={styles.dynamicTurntable}>
      <canvas id='myCanvas' />
    </div>
  )
}
        
export default DynamicTurntable